$text-color: #707070;


.menu {
  display: flex;
  list-style-type: none;
  flex-wrap: wrap;
  &__element {
    margin-right: 35px;
    &:last-child {
      margin-right: 0; } }
  &__link {
    font-size: 12px;
    text-transform: uppercase;
    color: $text-color;
    font-family: 'Raleway';
    line-height: 1.667;
    display: inline-block;
    text-decoration: none;
    transition-duration: 500ms;
    transition-property: opacity;
    &:hover {}
    &::before {
      content: ' ';
      width: 8px;
      height: 2px;
      background-color: $second;
      display: block;
      margin-bottom: 2px;
      opacity: 0;
      transition-duration: 500ms;
      transition-property: opacity; }
    &:hover {
      color: $second;
      &::before {
        opacity: 1; } } } }
@media (max-width: 768px) {
  .menu__element {
    margin-right: 20px; } }



