@charset "UTF-8";

/**
 * Основные стили
 **/
* {
	margin: 0;
	padding: 0; }

html {
	font-size: 16px;
	color: $black; }


body {
	font-family: 'Roboto', 'Arial', sans-serif;
	overflow-wrap: break-word;
	word-wrap: break-word;
	font-family: $fonts; }


.container {
	@include min-max(320px, 100%, 0);
	background-color: $primary; }


.wrap {
	@include min-max(320px, 1200px, 15px); }
