$color-text: #555555;

.banner {
  background: url('../img/bg-man.jpg') center top no-repeat;
  background-size: cover;
  &__wrap {
    padding-top: 255px;
    padding-bottom: 250px; }
  &__info {
    max-width: 580px;
    margin-left: auto; }
  &__title {
    font-size: 80px;
    font-family: $fonts;
    text-transform: uppercase;
    color: $white;
    font-weight: 800;
    line-height: 1;
    margin: 0; }
  &__little {
    font-size: 43px;
    display: block; }

  &__red {
    color: $second; }
  &__text {
    color: $color-text;
    font-size: 18px;
    font-weight: 800;
    text-transform: uppercase;
    line-height: 1.3323;
    margin: 15px 0; }
  &__button {
    @include button() {
      display: inline-block;
      margin-top: 20px;
      margin-bottom: 20px;
      margin-right: 25px; }
    &--last {
      border-color: $white;
      background-color: transparent;
      margin-right: 0; } } }

@media (max-width: 980px) {
  .banner {
    background: none;
    &__wrap {
      padding-top: 70px;
      padding-bottom: 50px; }
    &__info {
      max-width: 100%; } } }

@media (max-width: 768px) {
  .banner {
    &__title {
      font-size: 50px; }
    &__little {
      font-size: 25px; }
    &__text {
      font-size: 16px; } } }

@media (max-width: 480px) {
  .banner {
    &__title {
      font-size: 40px; }
    &__little {
      font-size: 20px; }
    &__text {
      font-size: 15px; }
    &__button {
      margin-top: 10px;
      margin-bottom: 10px; } } }







